body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.game {
    width: 450px;
    margin: auto;
    margin-top: 25px;
}

.game table {
    border: 1px black solid;
    width: 100%;
}

.game table td {
  width: 50px;
  height: 50px;
}

.square {
    width: 50px;
    height: 50px;
    cursor: pointer;
    text-align: center;
    line-height: 50px;
    font-size: 1.5em;
}

.hidden {
    background-color: black;
}

.startButton {
    display: block;
    width: 100%;
    margin: 20px 0;
    font-size: 1.2em;
}

.message {
    text-align: center;
    font-size: 1.2em;
}
